import React, { Component, PropTypes } from 'react';
import { connect } from 'react-redux';
import { browserHistory } from 'react-router';
import Step1 from '../../components/Step1';
import { trackEvent } from '../../services/analytics';

class Step1Container extends Component {
  constructor() {
    super();
    this.goNext = this.goNext.bind(this);
  }

  componentDidMount() {
    window.scrollTo(0, 0);
    if (window.location.hostname !== 'localhost') {
      window.onbeforeunload = function () {
        return true;
      };
    }
  }

  addItem(slug) {
    trackEvent('ajouter-panier', slug);
    this.props.dispatch({ type: 'ORDER.ADD_ITEM', item: { slug } });
  }

  removeItem(slug) {
    trackEvent('enlever-panier-etape1', slug);
    this.props.dispatch({ type: 'ORDER.REMOVE_ITEM_BY_SLUG', slug });
  }

  isOrderValid() {
    return this.props.clothes && this.props.clothes.length > 0;
  }

  goNext() {
    if (this.isOrderValid()) {
      browserHistory.push('/order/step2');
    } else {
      trackEvent('erreur', 'step1_no-cloth-selected');
    }
  }

  render() {
    return (
      <Step1
        clothes={this.props.clothes}
        addItem={slug => this.addItem(slug)}
        removeItem={slug => this.removeItem(slug)}
        isNotValid={!this.isOrderValid()}
        goNext={this.goNext}
      />
    );
  }
}

Step1Container.propTypes = {
  clothes: PropTypes.arrayOf(PropTypes.shape({})).isRequired,
  dispatch: PropTypes.func.isRequired,
};

const mapStateToProps = state => ({
  clothes: state.order.clothes,
});

const mapDispatchToProps = dispatch => ({ dispatch });

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(Step1Container);



