import React, { PropTypes } from 'react';
import { Link } from 'react-router';

import '../App.css';
import ClothesSelector from './ClothesSelector';
import LogoImg from '../assets/logo1.png';
import { trackEvent } from '../services/analytics';

import { Wrapper, Block, Header2, Title2, Title3, Logo, Button3, Center, Highlight } from './styledComponents';
import ProgressBar from './order/ProgressBar';

const Step1 = ({ clothes, addItem, removeItem, isNotValid, goNext }) => (
  <Wrapper>
    <Block>
      <Header2>
        <Link
          to={'/'} style={{ flex: 1, textAlign: 'center' }}
          onClick={() => trackEvent('click', 'home_step1')}
        >
          <Logo src={LogoImg} />
        </Link>
        <ProgressBar step={1} />
        <Title2>
          Je fais mon <Highlight>estimation</Highlight>
        </Title2>
        <Title3>
          Je choisis mes vêtements
        </Title3>
      </Header2>
      <ClothesSelector
        clothes={clothes}
        addItem={slug => addItem(slug)}
        removeItem={slug => removeItem(slug)}
      />
      <Center>
        <Button3 isNotValid={isNotValid} onClick={goNext}>{'> suivant <'}</Button3>
      </Center>
    </Block>
  </Wrapper>
);

Step1.propTypes = {
  goNext: PropTypes.func.isRequired,
  clothes: PropTypes.arrayOf(PropTypes.shape()).isRequired,
  addItem: PropTypes.func.isRequired,
  removeItem: PropTypes.func.isRequired,
  isNotValid: PropTypes.bool.isRequired,
};

export default Step1;



